import React, { useEffect } from "react";
import { useSearchParams } from "react-router-dom";
// import { AxiosError } from "axios";
import Box from "@mui/material/Box";
import { DotLoader } from "react-spinners";
import useAxiosApi from "@hooks/useAxiosApi";
import { auth as authApis } from "@utils/apis";
import { SigninApiPayload } from "@interfaces/payloads";
import { SigninApiResponse } from "@interfaces/responses";

const Signin = () => {
  const [searchParams] = useSearchParams();
  const userId = searchParams.get("userId") ?? "";
  const token = searchParams.get("token") ?? "";

  const { execute: signin } = useAxiosApi<SigninApiResponse, SigninApiPayload>(
    authApis.signin
  );

  useEffect(() => {
    (async function () {
      try {
        await signin({
          userId,
          token
        });
      } catch (error) {
        // const { response } = error as AxiosError<SigninApiResponse>;
        // if (response && response.data.redirectUrl) {
        //   window.location.href = response.data.redirectUrl;
        // }
      }
    })();
  }, [userId, token]);

  return (
    <Box width="100vw" height="100vh" display="flex" justifyContent="center">
      <Box margin="auto" display="inline-block">
        <DotLoader color="#2F6ECB " size={100} />
      </Box>
    </Box>
  );
};

export default Signin;
